.stars {
  height: 60px;
  display: flex;
  align-items: center;
  cursor: pointer;
  &:hover {
    .star polygon {
      fill: #f79b35 !important;
    }
  }
  .star {
    float: left;
    polygon {
      fill: #d8d8d8;
    }
    &:hover~.star {
      polygon {
        fill: #d8d8d8 !important;
      }
    }
  }
  &[data-stars] {
    .star polygon {
      fill: #f79b35;
    }
  }
  &[data-stars="1"] {
    .star:nth-child(1)~.star polygon {
      fill: #d8d8d8;
    }
  }
  &[data-stars="2"] {
    .star:nth-child(2)~.star polygon {
      fill: #d8d8d8;
    }
  }
  &[data-stars="3"] {
    .star:nth-child(3)~.star polygon {
      fill: #d8d8d8;
    }
  }
  &[data-stars="4"] {
    .star:nth-child(4)~.star polygon {
      fill: #d8d8d8;
    }
  }
  &[data-stars="5"] {
    .star:nth-child(5)~.star polygon {
      fill: #d8d8d8;
    }
  }
}