.styled-checkbox {
  position: absolute;
  opacity: 0;
}

.styled-checkbox + label {
  position: relative;
  cursor: pointer;
  padding: 0;
}

.styled-checkbox + label:before {
  content: '';
  margin-right: 10px;
  display: inline-block;
  vertical-align: text-top;
  width: 19px;
  height: 19px;
  background: white;
}

.styled-checkbox:hover + label:before {
  background: #fff;
  border: solid 1px #e1e1e1;
}

.styled-checkbox:checked + label:before {
  background: #fff;
  border: solid 1px #e1e1e1;
}

.styled-checkbox:checked + label:after {
  content: '';
  position: absolute;
  left: 4px;
  top: 8px;
  background: #006dac;
  width: 2px;
  height: 2px;
  box-shadow: 2px 0 0 #006dac, 4px 0 0 #006dac, 4px -2px 0 #006dac, 4px -4px 0 #006dac, 4px -6px 0 #006dac, 4px -8px 0 #006dac;
  transform: rotate(45deg);
}

.switch {
  position: relative;
  display: flex;
  height: 36px;
  width: 122px;
  border-radius: 3px;
  background-color: #ffffff;
  overflow: hidden;
}

.switch:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  display: block;
  border: solid 1px #d7d7d7;
}

.switch-label {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  width: 50%;
  font-size: 16px;
  color: #8c8c8c;
  text-align: center;
  cursor: pointer;
}

.switch-input {
  display: none;
}

.switch-input:checked + .switch-label {
  color: #fff;
  transition: 0.15s ease-out;
}

.switch-input:checked + .switch-label-on ~ .switch-selection {
  left: 62px;
}

.switch-selection {
  display: block;
  position: absolute;
  z-index: 1;
  top: 0px;
  left: 0px;
  width: 62px;
  height: 100%;
  background-color: #4d9fce;
