.time__header {
  position: relative;
  display: flex;
  justify-content: space-between;
  font-family: "ProximaNovaSemibold", Arial, sans-serif;
  font-size: 14px;
  font-weight: 600;
  line-height: normal;
  text-align: left;
  color: #bababa;
}

.time__header:before {
  top: -3px;
  width: 3px;
  background-image: -webkit-gradient(linear, 0 100%, 0 0, from(#000), to(transparent));
  background-image: -webkit-linear-gradient(transparent, #000);
  background-image: -moz-linear-gradient(transparent, #000);
  background-image: -o-linear-gradient(transparent, #000);
}

.time__header:after {
  right: -3px;
  height: 3px;
  background-image: -webkit-gradient(linear, 0 0, 100% 0, from(#000), to(transparent));
  background-image: -webkit-linear-gradient(left, #000, transparent);
  background-image: -moz-linear-gradient(left, #000, transparent);
  background-image: -o-linear-gradient(left, #000, transparent);
}

.time__cell {
  position: relative;
  flex-basis: 34%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 10px 0;
}

.time__cell:before, .time__cell:after {
  content: "";
  display: inline-block;
  width: 100%;
  height: 1px;
  position: absolute;
  left: 0;
  z-index: 100;
}

.time__cell:before {
  height: 2px;
  top: 0;
}

.time__cell:after {
  bottom: 0;
}

.time__cell--highlight {
  background-color: #006eac;
  color: #ffffff;
}

.time__cell--highlight .time__day,
.time__cell--highlight .time__month {
  font-size: 18px;
  color: #bcdff2;
}

.time__cell--highlight .time__month {
  margin-left: 5px;
}

.time__cell--left:before {
  background: -moz-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #b7b7b7 50%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #b7b7b7 50%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #b7b7b7 50%);
}

.time__cell--left:after {
  background: -moz-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #b7b7b7 50%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0) 0%, #b7b7b7 50%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, rgba(255, 255, 255, 0) 0%, #b7b7b7 50%);
}

.time__cell--right:before {
  background: -moz-linear-gradient(left, #b7b7b7 50%, rgba(255, 255, 255, 0) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #b7b7b7 50%, rgba(255, 255, 255, 0) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #b7b7b7 50%, rgba(255, 255, 255, 0) 100%);
}

.time__cell--right:after {
  background: -moz-linear-gradient(left, #b7b7b7 50%, rgba(255, 255, 255, 0) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #b7b7b7 50%, rgba(255, 255, 255, 0) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #b7b7b7 50%, rgba(255, 255, 255, 0) 100%);
}

